import { SectionCard } from "../card/SectionCard";
import { SectionTitle } from "../formElements/SectionTitle";
import { Link, Text } from "gestalt";
import { InfoCardDiv } from "./InfoCardDiv";

export const HRAInfo = () => {

    const listItem = (message: any) => {
        return (
            <li>
                <Text size="200">
                    {message}
                </Text>
            </li>
        )
    }

    const listStyle = {
        fontSize: "small",
        marginTop: '16px',
        marginBottom: '16px',
        marginLeft: '16px'
    }

    const content = () => (
        <InfoCardDiv>
            <Text size="200">
                The exemption on HRA is calculated as per 2A of the Income Tax Rules. As per Rule 2A, the least of the following is exempted from salary under Section 10(13A) and does not form part of the taxable income.
            </Text>
            <ul style={listStyle}>
                {listItem("Actual HRA received from the employer")}
                {listItem("For those living in metro cities: 50% of (Basic salary + Dearness allowance)")}
                {listItem("For those living in non-metro cities: 40% of (Basic salary + Dearness allowance)")}
                {listItem("Actual rent paid minus 10% of (Basic salary + Dearness allowance)")}

            </ul>
            <Text size="200">
                Read more about HRA. <Link target="blank" externalLinkIcon="default" display="inline" href="https://www.iciciprulife.com/insurance-guide/financial-planning-tools-calculators/hra-exemption-calculator.html">Click here</Link>
            </Text>
        </InfoCardDiv>
    )

    return (
        <SectionCard>
            <SectionTitle title={"HRA Calculation method"} sx={{ mb: 2, mt: 2 }} />
            {content()}
        </SectionCard>
    )
}