import { Deduction, Investment, NonTaxableExpense, calculateNetIncome } from "@harshit-kushwaha/tax-calculator-lib"
import { TableRow, TableCell, Grid, TableBody } from "@mui/material"
import { BannerSlim } from "gestalt"
import { useSelector } from "react-redux"
import { SalaryBreakup } from "../types/SalaryBreakup.type"
import { FormTable } from "./formElements/FormTable"
import { SalaryComponentOutput } from "./formElements/SalaryComponentOutput"
import { SectionMessage } from "./formElements/SectionMessage"
import { Investment as SalaryInvestment } from "../types/Investment.type"

type NetSalaryComponentProps = {
    selectedRegimeId: string
}

export const NetSalaryComponents = (props: NetSalaryComponentProps) => {

    const { selectedRegimeId } = props

    const salaryBreakup = useSelector<any, SalaryBreakup>(state => state.salaryBreakup)
    const taxRegimeId = selectedRegimeId
    const investments: SalaryInvestment[] = useSelector<any, SalaryInvestment[]>(state => state.rentAndInvestments.investments)
    const monthlyProfessionalTax = useSelector<any, number>(state => state.professionalTax.monthlyProfessionalTax)
    const monthlyRent: number = useSelector<any, number>(state => state.rentAndInvestments.monthlyRent)
    const isMetroCity: boolean = useSelector<any, boolean>(state => state.rentAndInvestments.metroCity)

    if (isSalaryBreakupZero(salaryBreakup) || !taxRegimeId) {
        return <></>
    }

    const annualProfessionalTax = monthlyProfessionalTax * 12

    const nonTaxableExpenses: NonTaxableExpense = nonTaxableExpense(salaryBreakup.providendFund, investments, monthlyRent, isMetroCity)

    const incomeWithoutBonus = calculateNetIncome(
        taxRegimeId,
        { ...salaryBreakup, providendFund: { amount: salaryBreakup.providendFund }, professionalTax: annualProfessionalTax, bonus: 0, variable: 0 },
        nonTaxableExpenses
    )

    const incomeWithBonus = calculateNetIncome(
        taxRegimeId,
        { ...salaryBreakup, providendFund: { amount: salaryBreakup.providendFund }, professionalTax: annualProfessionalTax },
        nonTaxableExpenses
    )

    const isRebateApplicable = incomeWithoutBonus.rebateAmount > 0

    const annualNetSalary = Math.round(incomeWithoutBonus.netIncome)
    const monthlyNetSalary = Math.round(incomeWithoutBonus.netIncome / 12)
    const taxableIncome = Math.round(incomeWithoutBonus.taxableIncome)
    const incomeTax = Math.round(incomeWithoutBonus.incomeTax)
    const incomeTaxForBonusAndVariable = Math.round(incomeWithBonus.incomeTax - incomeWithoutBonus.incomeTax)
    const variableTax = incomeTaxForBonusAndVariable > 0 ? Math.round(incomeTaxForBonusAndVariable * (salaryBreakup.variable / (salaryBreakup.variable + salaryBreakup.bonus))) : 0
    const bonusTax = incomeTaxForBonusAndVariable > 0 ? Math.round(incomeTaxForBonusAndVariable * (salaryBreakup.bonus / (salaryBreakup.variable + salaryBreakup.bonus))) : 0
    const netVariable = salaryBreakup.variable - variableTax
    const netBonus = salaryBreakup.bonus - bonusTax

    const effectiveAnnualNetSalary = annualNetSalary + incomeTax
    const effectiveMonthlyNetSalary = Math.round(effectiveAnnualNetSalary / 12)
    const effectiveTaxableIncome = 0
    const effectiveIncomeTax = 0

    const salaryDeductions = incomeWithoutBonus.nonTaxSalaryDeductions

    const tableDivider = () => (
        <TableRow sx={{ backgroundColor: 'aliceblue' }}>
            <TableCell></TableCell>
            <TableCell></TableCell>
        </TableRow>
    )

    const effectiveRows = () => {
        return (<>
            {tableDivider()}
            <TableRow sx={{ backgroundColor: '#fff9c4' }}>
                <TableCell>Effective Salary In Hand (p.m.)</TableCell>
                <TableCell><SalaryComponentOutput outputValue={effectiveMonthlyNetSalary} strikedValue={monthlyNetSalary} /></TableCell>
            </TableRow>
            <TableRow sx={{ backgroundColor: '#fff9c4' }}>
                <TableCell>Effective Total Net Salary (p.a.)</TableCell>
                <TableCell><SalaryComponentOutput outputValue={effectiveAnnualNetSalary} strikedValue={annualNetSalary} /></TableCell>
            </TableRow>
            <TableRow sx={{ backgroundColor: '#fff9c4' }}>
                <TableCell>Effective Total Taxable Income</TableCell>
                <TableCell><SalaryComponentOutput outputValue={effectiveTaxableIncome} strikedValue={taxableIncome} /></TableCell>
            </TableRow>
            <TableRow sx={{ backgroundColor: '#fff9c4' }}>
                <TableCell>Effective Total Tax Payable</TableCell>
                <TableCell><SalaryComponentOutput outputValue={effectiveIncomeTax} strikedValue={incomeTax} /></TableCell>
            </TableRow>
        </>)
    }

    const chartData = [
        ["Component", "Amount (p.m.)"],
        ["Net (In Hand) Salary", isRebateApplicable ? effectiveMonthlyNetSalary : monthlyNetSalary],
        ["Income Tax", Math.round((isRebateApplicable ? effectiveIncomeTax : incomeTax) / 12)],
        ["Professional Tax", Math.round(incomeWithoutBonus.professionalTax / 12)]
    ]

    salaryDeductions.forEach(deduction => chartData.push([deduction.displayName, Math.round(deduction.amount / 12)]))

    const otherSalaryDeductions = () => {
        return salaryDeductions.map(salaryDeduction => (
            <TableRow>
                <TableCell>{salaryDeduction.displayName} (monthly)</TableCell>
                <TableCell><SalaryComponentOutput outputValue={Math.round(salaryDeduction.amount / 12)} /></TableCell>
            </TableRow>
        ))
    }

    const bonusAndVariable = () => {
        return ((salaryBreakup.bonus > 0 || salaryBreakup.variable > 0) &&
            <>
                {tableDivider()}
                {salaryBreakup.variable > 0 &&
                    <>
                        <TableRow>
                            <TableCell>Net Variable Pay</TableCell>
                            <TableCell><SalaryComponentOutput outputValue={Math.round(netVariable)} /></TableCell>
                        </TableRow>
                        <TableRow>
                            <TableCell>Tax on Variable Pay</TableCell>
                            <TableCell><SalaryComponentOutput outputValue={Math.round(variableTax)} /></TableCell>
                        </TableRow>
                    </>
                }
                {salaryBreakup.bonus > 0 &&
                    <>
                        <TableRow>
                            <TableCell>Net Bonus</TableCell>
                            <TableCell><SalaryComponentOutput outputValue={Math.round(netBonus)} /></TableCell>
                        </TableRow>
                        <TableRow>
                            <TableCell>Tax on Bonus</TableCell>
                            <TableCell><SalaryComponentOutput outputValue={Math.round(bonusTax)} /></TableCell>
                        </TableRow>
                    </>
                }
            </>
        )
    }

    const bannerTaxRate = () => {
        const percentage = isRebateApplicable ? 0 : roundToTwoDecimal(totalTaxPayable() / totalGrossSalary() * 100)
        return <BannerSlim
            iconAccessibilityLabel="Information"
            message={`Your effective tax rate is ${percentage}%.`}
            type="info"
        />
    }

    const roundToTwoDecimal = (value: number) => {
        return Math.round(value * 100) / 100
    }

    const totalTaxableIncome = () => {
        return Math.round(incomeWithBonus.taxableIncome)
    }

    const totalGrossSalary = () => {
        return Math.round(
            (Object.values(salaryBreakup).reduce((acc, curr) => acc + curr, 0))
        )
    }

    const totalTaxPayable = () => {
        return Math.round(incomeTax + bonusTax + variableTax)
    }

    const netSalaryComponents = () => {
        return (
            <>
                <Grid item xs={12} md={12}>
                    {isRebateApplicable && <SectionMessage info message="Hurray! You are elligible for a tax rebate." />}
                    {bannerTaxRate()}
                    <FormTable size="small" stickyHeader sx={{ mt: 2 }}>
                        <TableBody>
                            <TableRow>
                                <TableCell>Net Salary (monthly)</TableCell>
                                <TableCell><SalaryComponentOutput outputValue={monthlyNetSalary} /></TableCell>
                            </TableRow>
                            <TableRow>
                                <TableCell>Income Tax (monthly)</TableCell>
                                <TableCell><SalaryComponentOutput outputValue={Math.round(incomeTax / 12)} /></TableCell>
                            </TableRow>
                            <TableRow>
                                <TableCell>Professional Tax (monthly)</TableCell>
                                <TableCell><SalaryComponentOutput outputValue={Math.round(incomeWithoutBonus.professionalTax / 12)} /></TableCell>
                            </TableRow>
                            {otherSalaryDeductions()}
                            {bonusAndVariable()}
                            {tableDivider()}
                            <TableRow>
                                <TableCell>Total Net Salary (p.a.)</TableCell>
                                <TableCell><SalaryComponentOutput outputValue={annualNetSalary + netBonus + netVariable} /></TableCell>
                            </TableRow>
                            <TableRow>
                                <TableCell>Total Taxable Income</TableCell>
                                <TableCell><SalaryComponentOutput outputValue={totalTaxableIncome()} /></TableCell>
                            </TableRow>
                            <TableRow>
                                <TableCell>Total Tax Payable</TableCell>
                                <TableCell><SalaryComponentOutput outputValue={incomeTax + bonusTax + variableTax} /></TableCell>
                            </TableRow>
                            {
                                isRebateApplicable && effectiveRows()
                            }
                        </TableBody>
                    </FormTable>
                </Grid>
            </>
        )
    }

    return netSalaryComponents()

}


const nonTaxableExpense = (pfAmount: number, investments: SalaryInvestment[], monthlyRent: number, isMetroCity: boolean): NonTaxableExpense => {

    const investment_80c: Investment = {
        name: Deduction.INV_80_C,
        amount: pfAmount
    }

    const otherInvestments: Investment[] = []

    investments.forEach(investment => {
        if (investment.name === Deduction.INV_80_C) {
            investment_80c.amount += investment.value
        } else {
            otherInvestments.push({ name: investment.name, amount: investment.value })
        }
    })

    return {
        investments: [investment_80c, ...otherInvestments],
        rent: monthlyRent > 0
            ? {
                amount: monthlyRent * 12,
                // TODO: ADD THIS IN UI
                isMetro: isMetroCity
            }
            : undefined
    }
}

const isSalaryBreakupZero = (salaryBreakup: any): boolean => {
    const totalAmount = Object.keys(salaryBreakup)
        .map(key => salaryBreakup[key] as number)
        .reduce((total, componentAmount) => total + componentAmount)

    return totalAmount === 0
}