import { SectionMessage } from "./SectionMessage"



type SectionNotApplicableProps = {
    sectionName: string
}

export const SectionNotApplicable = (props: SectionNotApplicableProps) => {

    const { sectionName } = { ...props }

    const message: string = `${sectionName} is not applicable.`

    return <SectionMessage message={message} />

}