import { SectionCard } from "../card/SectionCard";
import { SectionTitle } from "../formElements/SectionTitle";
import { Accordion, Link, Text } from "gestalt";
import { InfoCardDiv } from "./InfoCardDiv";

export const InvestmentCategoriesInfo = () => {

    const subHeading = (heading: string) => (
        <Text size="200" weight="bold">
            {heading}
        </Text>
    )

    const text = (text: any) => (
        <Text size="200">
            {text}
        </Text>
    )

    const content = () => (
        <InfoCardDiv>
            {subHeading("Section 80 C")}
            {text("Section 80C allows deductions on investments and expenses. It includes options like EPF, PPF, life insurance premiums, home loan principal, tuition fees, NPS, and more. The maximum deduction limit is 1.5 lakh per year.")}
            <br />
            {subHeading("Section 80 CCD(1B) NPS Investment")}
            {text("Section 80CCD(1B) is a provision that offers additional deductions for contributions made towards the National Pension Scheme (NPS). Taxpayers can claim deductions up to 50,000 over and above the limit of Section 80C. It is an effective way to save on taxes while planning for retirement.")}
            <br />
            {subHeading("Section 80 CCD(2) Employer NPS")}
            {text("Section 80CCD(2) allows employees to claim additional deductions on contributions made by their employers towards the National Pension Scheme (NPS). The deduction can be up to 10% of the employee's salary, including basic pay and dearness allowance. This deduction is in addition to the deductions available under Section 80C and 80CCD(1). It provides an extra tax benefit for individuals who have an employer-sponsored NPS account.")}
            <br />
            {subHeading("Section 80 D Health Insurance")}
            {text("Section 80D allows individuals to claim deductions on health insurance premiums paid for self, family, and parents. The maximum deduction limit is 25,000 for self, family, and 25,000 for parents. For senior citizens, the limit is 50,000.")}
            <br />
            {subHeading("Section 24 (B) Home Loan Interest")}
            {text("Section 24(B) allows deductions on home loan interest payments. The maximum deduction limit is 2 lakh per year for self-occupied properties. For rented properties, there is no limit on the deduction amount.")}
            <br />
            {subHeading("Section 80 EE Home Loan Interest")}
            {text("Section 80EE allows deductions on home loan interest payments for first-time homebuyers. The maximum deduction limit is 50,000 per year. It is available for loans sanctioned between 1st April 2016 and 31st March 2017.")}
            <br />
            {subHeading("Section 80 E Education Loan Interest")}
            {text("Section 80E allows deductions on interest paid on education loans. There is no upper limit on the deduction amount. It is available for a maximum of 8 years or until the interest is paid, whichever is earlier.")}
            <br />
            {subHeading("Section 80 G Donations")}
            {text("Section 80G allows deductions on donations made to charitable organizations. The deduction amount varies based on the type of donation and the organization. It can be up to 100% of the donated amount.")}
            <br />
            <Text size="200">
                Read more in detail here (<Link target="blank" externalLinkIcon={"default"} display="inline" href="https://www.incometax.gov.in/iec/foportal/help/individual/return-applicable-0/#taxdeductions">www.incometax.gov.in.</Link>).
            </Text>
        </InfoCardDiv>
    )

    return (
        <SectionCard>
            <SectionTitle title={"Investment Categories"} sx={{ mb: 2, mt: 2 }} />
            <Accordion.Expandable
                id="accordionExample - header expandable"
                accessibilityCollapseLabel="collapse"
                accessibilityExpandLabel="expand"
                borderStyle="none"
                items={[
                    {
                        children: content(),
                        summary: ['Expand'],
                        title: 'Categories',
                    },
                ]}
            />
        </SectionCard>
    )
}