import { Grid } from "@mui/material"
import { useDispatch, useSelector } from "react-redux"
import { RECOMMENDED_TAX_REGIME, listTaxRegimesForYearId } from "@harshit-kushwaha/tax-calculator-lib"
import { selectTaxRegime } from "../store/taxRegime"
import { useEffect } from "react"
import { SectionTitle } from "./formElements/SectionTitle"
import { Icon } from "gestalt"
import "./TaxRegimeSelector.css"
import { CustomButton } from "./formElements/CustomButton"


export const TaxRegimeSelector = () => {

    const selectedRegime = useSelector<any, string>(state => state.taxRegime.selectedRegime)
    const selectedFinancialYear = useSelector<any, string>(state => state.financialYear.selectedYear)
    const regimes = listTaxRegimesForYearId(selectedFinancialYear)

    const dispatch = useDispatch()

    const defaultRegime = selectedRegime ?? regimes[0]?.id

    useEffect(() => {
        dispatch(selectTaxRegime(defaultRegime))
    }, [defaultRegime, dispatch])

    const handleTaxRegimeSelection = (event: any) => {
        dispatch(selectTaxRegime(event.target.value))
    }

    const renderRegimes = (regimes: any[]) => (
        <Grid item xs={12} sx={{ pl: 2, pr: 2 }}>
            <Grid container item spacing={4} rowSpacing={2}>
                {
                    regimes.map(regime => (
                        <Grid item xs={12} sm={4}>
                            <CustomButton
                                disableRipple
                                onClick={handleTaxRegimeSelection}
                                value={regime.id}
                                variant={selectedRegime === regime.id ? "outlined" : "text"}
                                className={selectedRegime === regime.id ? "selected-button" : ""}
                                disableFocusRipple
                            >{regime.name}</CustomButton>
                        </Grid>)
                    )
                }
                <Grid item xs={12} sm={4}>
                    <CustomButton 
                        variant={selectedRegime === RECOMMENDED_TAX_REGIME? "outlined" : "text"}
                        value={RECOMMENDED_TAX_REGIME}
                        disableRipple 
                        backgroundGradient={true}
                        disableFocusRipple 
                        disabled
                        onClick={handleTaxRegimeSelection}
                    >
                        <Icon icon="sparkle" accessibilityLabel={"recommend"} color="recommendation" /> &nbsp; Help me Decide
                    </CustomButton>
                </Grid>
            </Grid>
        </Grid>
    )

    const isEmpty = () => {
        return !(selectedFinancialYear && regimes.length)
    }

    return (<>
        {isEmpty()
            ? <></>
            : <Grid container xs={12}>
                <SectionTitle title="Tax Regime" />
                {renderRegimes(regimes)}
            </Grid>
        }
    </>)
}


