import { Button, ButtonProps } from '@mui/material';
import { styled } from '@mui/system';

const selectedButtonStyle = {
    borderRadius: "12px",
    color: "black",
    borderColor: "rgb(146, 74, 247)",
    borderWidth: "1px",
    paddingVertical: "4px",
    paddingHorizontal: "14px",
    borderGradient: "linear-gradient(to right, #f17f7f 0%, #60c6a4 19%, #29b6d9 42%, #7742d7 79%, #27bfe5 100%))",
    hoverBackground: "rgb(146, 74, 247, 0.1)"
};

interface CustomButtonProps extends ButtonProps { 
    backgroundGradient?: boolean
}

export const CustomButton = styled(Button)<CustomButtonProps>(() => ({
    borderRadius: selectedButtonStyle.borderRadius,
    textTransform: "capitalize",
    color: selectedButtonStyle.color,
    borderColor: selectedButtonStyle.borderColor,
    borderImage: selectedButtonStyle.borderGradient,
    borderWidth: selectedButtonStyle.borderWidth,
    '&:hover': {
        borderColor: selectedButtonStyle.borderColor,
        borderImage: selectedButtonStyle.borderGradient,
        background: selectedButtonStyle.hoverBackground
    }
}));
