import { createSlice } from "@reduxjs/toolkit";

const initialState = {
    monthlyProfessionalTax: 0,
}

// Reducer
const slice = createSlice({
    name: 'professionalTax',
    initialState,
    reducers: {
        setMonthlyProfessionalTax: (state, action) => {
            state.monthlyProfessionalTax = action.payload;
        },
        resetProfessionalTax: () => initialState
    },
});

export default slice.reducer

// Actions
export const { setMonthlyProfessionalTax, resetProfessionalTax } = slice.actions