import { createSlice } from "@reduxjs/toolkit";
import { Investment } from "../types/Investment.type";

type InitialStateType = {
    monthlyRent: number,
    investments: Investment[],
    metroCity: boolean
}

const initialState: InitialStateType = {
    monthlyRent: 0,
    investments: [],
    metroCity: false
}

// Reducer
const slice = createSlice({
    name: 'rentAndInvestments',
    initialState,
    reducers: {
        setMonthlyRent: (state, action) => {
            state.monthlyRent = action.payload;
        },
        setMetroCity: (state, action) => {
            state.metroCity = action.payload
        },
        setInvestments: (state, action) => {
            state.investments = action.payload;
        },
        removeInvestment: (state, action) => {
            const index = state.investments.findIndex((investment: any) => investment.name === action.payload)
            if (index >= 0) {
                state.investments.splice(index, 1)
            }
        },
        addInvestment: (state, action) => {
            state.investments.push(action.payload)
        },
        updateInvestment: (state, action) => {
            const investment = state.investments.find((investment: any) => investment.name === action.payload.name)
            if (investment) {
                investment.value = action.payload.value
            }
        },
        resetRentAndInvestments: () => initialState
    },
});

export default slice.reducer

// Actions
export const { setInvestments, setMonthlyRent, removeInvestment, addInvestment, resetRentAndInvestments, updateInvestment, setMetroCity } = slice.actions