import { createSlice } from "@reduxjs/toolkit";

const initialState = {
    selectedRegime: undefined
}

// Reducer
const slice = createSlice({
    name: 'taxRegime',
    initialState,
    reducers: {
        setTaxRegime: (state, action) => {
            state.selectedRegime = action.payload;
        },
        resetTaxRegime: () => initialState
    },
});

export default slice.reducer

// Actions
const { setTaxRegime } = slice.actions

export const selectTaxRegime = (taxRegimeId: string) => {
    return setTaxRegime(taxRegimeId)
}

export const { resetTaxRegime } = slice.actions