import { Grid } from "@mui/material"
import { useEffect } from "react"
import { NetSalaryComponents } from "./NetSalaryComponents"
import { useSelector } from "react-redux";
import { RECOMMENDED_TAX_REGIME, listTaxRegimesForYearId } from "@harshit-kushwaha/tax-calculator-lib";
import { SectionTitle } from "./formElements/SectionTitle";

export const NetSalary = () => {

    useEffect(() => {
        window.scrollTo({ top: 0, left: 0, behavior: 'smooth' });
    }, []);

    const selectedTaxRegimeId = useSelector<any, string>(state => state.taxRegime.selectedRegime)
    const selectedFinancialYear = useSelector<any, string>(state => state.financialYear.selectedYear)
    const taxRegimes = listTaxRegimesForYearId(selectedFinancialYear)

    const isMultipleOutputNeeded = () => selectedTaxRegimeId === RECOMMENDED_TAX_REGIME

    const recommendation = () => (
        <>
            {
                taxRegimes.map((regime: any) => (
                    <Grid item xs={6}>
                        <NetSalaryComponents selectedRegimeId={regime.id} />
                    </Grid>
                ))
            }

        </>
    )

    const selectedRegimeOutput = () => (
        <NetSalaryComponents selectedRegimeId={selectedTaxRegimeId} />
    )

    return (
        <Grid container xs={12} spacing={3}>
            <SectionTitle title="Net (In Hand) Salary" />
            {isMultipleOutputNeeded() ? recommendation() : selectedRegimeOutput()}
        </Grid>
    )
}