import InfoOutlinedIcon from '@mui/icons-material/InfoOutlined';
import { Link } from '@mui/material';

type CustomInfoIconProps = {
    link: string
}

export const CustomInfoIcon = (props: CustomInfoIconProps) => {

    const { link } = { ...props }

    return (
        <Link href={link}>
            <InfoOutlinedIcon color={'info'} fontSize={'small'} sx={{ 'vertical-align': 'bottom' }} />
        </Link>
    )
}