import { Grid, Stack } from "@mui/material"
import { NewTaxRegimeInfo } from "../components/info/NewTaxRegimeInfo"
import { OldTaxRegimeInfo } from "../components/info/OldTaxRegimeInfo"
import { HRAInfo } from "../components/info/HRAInfo"
import { HRAMetroCitiesIndo } from "../components/info/HRAMetroCitiesInfo"
import { ProfessionalTaxInfo } from "../components/info/ProfesstionalTaxInfo"
import { InvestmentCategoriesInfo } from "../components/info/InvestmentCategoriesInfo"
import { About } from "../components/info/About"

export const InformationContainer = () => {

    const gridItem = (content: any, sm?: number) => (
        <Grid item xs={12}>
            {content}
        </Grid>
    )

    const infoCards = [
        <OldTaxRegimeInfo />,
        <NewTaxRegimeInfo />,
        <ProfessionalTaxInfo />,
        <HRAInfo />,
        <HRAMetroCitiesIndo />,
    ]

    const renderListInStack = (list: any) => (
        <Stack>
            {
                list.map((card: any) => (
                    gridItem(card)
                ))
            }
        </Stack>
    )

    const singleStack = () => (
        <Grid item xs={12} sx={{ display: { sm: 'none', xs: 'block' } }}>
            {renderListInStack(infoCards)}
        </Grid>
    )

    const doubleStack = () => {
        const stackLeft = infoCards.filter((_, index) => index % 2 === 0);
        const stackRight = infoCards.filter((_, index) => index % 2 !== 0);

        return <Grid container item xs={12} sx={{ display: { xs:'none', sm: 'flex' } }} spacing={4}>
            <Grid item xs={6}>{renderListInStack(stackLeft)}</Grid>
            <Grid item xs={6}>{renderListInStack(stackRight)}</Grid>
        </Grid>
    }

    const renderCards = () => (
        <Grid container item xs={12}>
            {singleStack()}
            {doubleStack()}
        </Grid>
    )

    return (<>
        <Grid container item xs={12} columnSpacing={4}>
            {gridItem(<InvestmentCategoriesInfo />, 12)}
            {renderCards()}
            {gridItem(<About />, 12)}
        </Grid>
    </>
    )

}