type AppLogoSmallProps = {
    inverted?: boolean,
    height?: string
}

export const AppLogoSmall = (props: AppLogoSmallProps) => {

    const { inverted, height } = props

    return (
        <a href="/">
            <img src={inverted ? "./inverted-logo.png" : "/logo192.png"} alt="App Logo" height={ height ?? "80px" } />
        </a>
    )
}