import { Box, FormLabel, Grid } from "@mui/material"
import SentimentVerySatisfiedRoundedIcon from '@mui/icons-material/SentimentVerySatisfiedRounded';

type SectionMessageProps = {
    message: string,
    info?: boolean
}

const sectionMessageStyle = {
    'text-align': 'center',
    display: 'block',
}

const sectionMessageOutline = {
    backgroundColor: '#fff9c4',
    'border-radius': '10px',
    'border-style': 'dotted',
    'border-width': 'thin',
}

export const SectionMessage = (props: SectionMessageProps) => {

    const { message, info } = { ...props }

    const infoMessage = () => (
        <Box sx={{ p: 2, b: 0 }} style={sectionMessageOutline}>
            <FormLabel style={sectionMessageStyle}>
                {<SentimentVerySatisfiedRoundedIcon color={'success'} fontSize="small" sx={{ 'vertical-align': 'sub', mr: 2 }} />}
                <em>{message}</em>
            </FormLabel>
        </Box>
    )

    const plainMessage = () => (
        <Box sx={{ p: 2, b: 0 }}>
            <FormLabel style={sectionMessageStyle}>
                <em>{message}</em>
            </FormLabel>
        </Box>
    )

    return (
        <Grid container xs={12} sx={{ mt: 2, mb: 2 }}>
            <Grid item xs={12}>
                {
                    info
                        ? infoMessage()
                        : plainMessage()
                }
            </Grid>
        </Grid>
    )
}