import { createSlice } from "@reduxjs/toolkit";

const initialState = {
    selectedYear: undefined
}

// Reducer
const slice = createSlice({
    name: 'financialYear',
    initialState,
    reducers: {
        setFinancialYear: (state, action) => {
            state.selectedYear = action.payload;
        },
        resetFinancialYear: () => initialState
    },
});

export default slice.reducer

// Actions
const { setFinancialYear } = slice.actions

export const selectFinancialYear = (financialYearId: string) => {
    return setFinancialYear(financialYearId)
}

export const { resetFinancialYear } = slice.actions