import { styled } from '@mui/material/styles';
import { ButtonGroup, Grid, Stack, Step, StepConnector, StepIconProps, StepLabel, Stepper, stepConnectorClasses } from "@mui/material"
import CalendarMonthRoundedIcon from '@mui/icons-material/CalendarMonthRounded';
import PieChartRoundedIcon from '@mui/icons-material/PieChartRounded';
import CurrencyRupeeRoundedIcon from '@mui/icons-material/CurrencyRupeeRounded';
import { useState } from 'react';
import { steps } from '../components/config/Step.config';
import { useDispatch, useSelector } from 'react-redux';
import { resetFinancialYear } from '../store/financialYear';
import { resetTaxRegime } from '../store/taxRegime';
import { resetSalaryBreakup } from '../store/salary';
import { resetNavigation } from '../store/navigation';
import MapsHomeWorkRoundedIcon from '@mui/icons-material/MapsHomeWorkRounded';
import { resetRentAndInvestments } from '../store/rentAndInvestment';
import { resetProfessionalTax } from '../store/professionalTax';
import ReactGA from "react-ga4";
import { SectionCard } from '../components/card/SectionCard';
import { Button } from 'gestalt';
import { BorderLinearProgress } from '../components/common/BorderLinearProgress';
import { SectionTitle } from '../components/formElements/SectionTitle';
import { IconButton } from 'gestalt';


const ColorlibConnector = styled(StepConnector)(({ theme }) => ({
    [`&.${stepConnectorClasses.alternativeLabel}`]: {
        top: 19,
    },
    [`&.${stepConnectorClasses.active}`]: {
        [`& .${stepConnectorClasses.line}`]: {
            backgroundColor: '#0074e8'
        },
    },
    [`&.${stepConnectorClasses.completed}`]: {
        [`& .${stepConnectorClasses.line}`]: {
            backgroundColor: '#0074e8'
        },
    },
    [`& .${stepConnectorClasses.line}`]: {
        height: 3,
        border: 0,
        backgroundColor:
            theme.palette.mode === 'dark' ? theme.palette.grey[800] : '#eaeaf0',
        borderRadius: 1,
    },
}));

const ColorlibStepIconRoot = styled('div')<{
    ownerState: { completed?: boolean; active?: boolean };
}>(({ theme, ownerState }) => ({
    backgroundColor: theme.palette.mode === 'dark' ? theme.palette.grey[700] : '#ccc',
    zIndex: 1,
    color: '#fff',
    width: '40px',
    height: '40px',
    display: 'flex',
    borderRadius: '33%',
    justifyContent: 'center',
    alignItems: 'center',
    ...(ownerState.active && {
        backgroundColor: '#0074e8'
    }),
    ...(ownerState.completed && {
        backgroundColor: '#0074e8',
    }),
}));

const IconStyle = {
    height: '16px',
    width: '16px'
}

function ColorlibStepIcon(props: StepIconProps) {
    const { active, completed, className } = props;

    const icons: { [index: string]: React.ReactElement } = {
        1: <CalendarMonthRoundedIcon style={IconStyle} />,
        2: <CurrencyRupeeRoundedIcon style={IconStyle} />,
        3: <MapsHomeWorkRoundedIcon style={IconStyle} />,
        4: <PieChartRoundedIcon style={IconStyle} />,
    };

    return (
        <ColorlibStepIconRoot ownerState={{ completed, active }} className={className}>
            {icons[String(props.icon)]}
        </ColorlibStepIconRoot>
    );
}

export default function StepperContainer() {

    const [activeStep, setActiveStep] = useState(0);

    const dispatch = useDispatch()

    const handleNext = () => {
        ReactGA.event({
            action: `next_button_click_to_step_${activeStep + 2}`,
            category: 'engagement',
            label: 'next',
        })
        setActiveStep(prevActiveStep => prevActiveStep + 1);
    };

    const handleBack = () => {
        ReactGA.event({
            action: `back_button_click_to_step_${activeStep}`,
            category: 'engagement',
            label: 'back',
        })
        setActiveStep(prevActiveStep => prevActiveStep - 1);
    };

    const handleReset = () => {
        dispatch(resetFinancialYear())
        dispatch(resetTaxRegime())
        dispatch(resetSalaryBreakup())
        dispatch(resetNavigation())
        dispatch(resetRentAndInvestments())
        dispatch(resetProfessionalTax())
        ReactGA.event({
            action: `reset_button_click_step_${activeStep + 1}`,
            category: 'engagement',
            label: 'reset',
        })
        setActiveStep(0)
    }

    const isNextButtonDisabled = useSelector<any, any>(state => state.navigation.nextDisabled)


    const isNextButtonShown = () => {
        return activeStep < steps.length - 1;
    }

    const isResetButtonShown = () => {
        return activeStep > 0
    }

    const isBackButtonShown = () => {
        return activeStep > 0
    }


    const renderButtons = () => (
        <Grid container xs={12} sx={{ justifyContent: 'flex-end' }} spacing={2} mt={2}>
            <Grid item xs={12} sm={3} md={2}>
                {isBackButtonShown() &&
                    <Button
                        onClick={handleBack}
                        // startIcon={<NavigateBeforeRoundedIcon />} 
                        fullWidth
                        text='Back'
                    />
                    //     {/* Back
                    // </Button> */}
                }
            </Grid>
            <Grid item xs={12} sm={3} md={2} >
                {isResetButtonShown() &&
                    <Button
                        onClick={handleReset}
                        color={"red"}
                        // variant="outlined" 
                        fullWidth
                        text='Reset'
                    />
                    //     {/* Reset
                    // </Button> */}
                }
            </Grid>
            <Grid item xs={12} sm={3} md={2}>
                {isNextButtonShown() &&
                    <Button
                        fullWidth
                        // variant="contained"
                        color="blue"
                        onClick={handleNext}
                        // endIcon={<NavigateNextRoundedIcon />}
                        disabled={isNextButtonDisabled[activeStep]}
                        text='Next'
                    />
                }
            </Grid>
        </Grid>
    )

    const buttonGroupStyle = {
        display: 'flex',
        justifyContent: 'space-between'
}

const renderMobileButtons = () => (
    <>
        <ButtonGroup style={buttonGroupStyle}>
            <IconButton
                accessibilityHaspopup
                accessibilityLabel="Back Button"
                icon="arrow-back"
                tooltip={{ text: 'Back', idealDirection: 'up' }}
                onClick={handleBack}
                disabled={!isBackButtonShown()}
            />
            {isResetButtonShown() && <IconButton
                accessibilityHaspopup
                accessibilityLabel="Reset Button"
                tooltip={{ text: 'Reset', idealDirection: 'up' }}
                onClick={handleReset}
                iconColor='red'
                icon='arrow-counter-clockwise'
            />}
            <IconButton
                accessibilityHaspopup
                accessibilityLabel="Next Button"
                tooltip={{ text: 'Next', idealDirection: 'up' }}
                icon="arrow-forward"
                disabled={isNextButtonDisabled[activeStep] || !isNextButtonShown()}
                onClick={handleNext}
            />
        </ButtonGroup>
    </>
)

const mobileView = () => (
    <Grid container xs={12} sx={{ display: { xs: 'block', sm: 'none' } }}>
        {/* <PageTitle title={steps[activeStep].title} /> */}
        <SectionTitle title={steps[activeStep].title} sx={{ mb: 2, mt: 2 }} />
        <BorderLinearProgress variant="determinate" value={((activeStep + 1) / steps.length) * 100} />
        {stepperContent()}
        {/* <Grid item sx={{ m: 2 }}>
                {isResetButtonShown() &&
                    <Button onClick={handleReset} color={"red"} fullWidth text="Reset" size="sm" />
                    //     Reset
                    // </Button>
                }
            </Grid> */}

        <SectionCard borderStyle='none'>
            {renderMobileButtons()}
        </SectionCard>

        {/* <MobileStepper
                variant="text"
                steps={steps.length}
                position="static"
                activeStep={activeStep}
                nextButton={
                    <Button
                        size="sm"
                        disabled={isNextButtonDisabled[activeStep] || !isNextButtonShown()}
                        onClick={handleNext}
                        text="Next"
                    />
                }
                backButton={
                    <Button
                        size="sm"
                        onClick={handleBack}
                        disabled={!isBackButtonShown()}
                        text="Back"
                    />
                    //     <KeyboardArrowLeftRoundedIcon />
                    //     Back
                    // </Button>
                }
            /> */}
    </Grid>
)

const stepperContent = () => (
    steps[activeStep].component
)

const nonMobileView = () => (
    <Grid container xs={12} sx={{ display: { xs: 'none', sm: 'block' } }}>
        <Stepper alternativeLabel activeStep={activeStep} connector={<ColorlibConnector />}>
            {steps.map((step) => (
                <Step key={step.key}>
                    <StepLabel StepIconComponent={ColorlibStepIcon}>{step.title}</StepLabel>
                </Step>
            ))}
        </Stepper>
        {stepperContent()}
        {renderButtons()}
    </Grid>
)

return (
    <SectionCard>
        <Stack sx={{ width: '100%' }} spacing={4}>
            {mobileView()}
            {nonMobileView()}
        </Stack>
    </SectionCard>
);
}
