import { createSlice } from "@reduxjs/toolkit";

const initialState = {
    nextDisabled: [false, false, false, false]
}

// Reducer
const slice = createSlice({
    name: 'navigation',
    initialState,
    reducers: {
        setNextDisabled: (state, action) => {
            state.nextDisabled[action.payload.stepId] = action.payload.disabled as boolean;
        },
        resetNavigation: () => initialState
    },
});

export default slice.reducer

// Actions
const { setNextDisabled } = slice.actions

export const setNextDisableForStep = (stepId: number, disabled: boolean) => {
    return setNextDisabled({ stepId, disabled })
}

export const { resetNavigation } = slice.actions