import { Grid, TableBody, TableHead, TableRow } from "@mui/material"
import { SalaryComponentInput } from "./formElements/SalaryComponentInput"
import { SalaryComponentOutput } from "./formElements/SalaryComponentOutput"
import { setAllowances, setBasic, setProvidendFund, setGratuity, setHra, setVariable, setBonus } from "../store/salary"
import { useDispatch, useSelector } from "react-redux"
import { useEffect } from "react"
import { setNextDisableForStep } from "../store/navigation"
import { SectionTitle } from "./formElements/SectionTitle"
import TableCell, { tableCellClasses } from "@mui/material/TableCell";
import { FormTable } from "./formElements/FormTable"
import { LITERALS } from "../utils/constants"
import { SalaryBreakup } from "../types/SalaryBreakup.type"

export const SalaryStructure = () => {

    const salaryBreakup = useSelector<any, SalaryBreakup>(state => state.salaryBreakup)
    const { basic, hra, otherAllowances, providendFund, gratuity, variable, bonus } = { ...salaryBreakup }

    const componentsTotal = (): number => (
        Object.values(salaryBreakup).reduce((acc, curr) => acc + curr, 0)
    )

    const dispatch = useDispatch()

    useEffect(() => {
        if (componentsTotal() === 0) {
            dispatch(setNextDisableForStep(1, true))
        } else {
            dispatch(setNextDisableForStep(1, false))
        }
    })

    const formInputs = () => {
        return (
            <>
                <FormTable size="small" stickyHeader sx={{ [`& .${tableCellClasses.root}`]: { borderBottom: "none" } }}>
                    <TableHead>
                        <TableRow>
                            <TableCell>Salary Component</TableCell>
                            <TableCell>Annual Amount (In {LITERALS.INDIAN_RUPEE})</TableCell>
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        <TableRow>
                            <TableCell>Basic</TableCell>
                            <TableCell><SalaryComponentInput value={basic} onInputChange={setBasic} /></TableCell>
                        </TableRow>
                        <TableRow>
                            <TableCell>HRA</TableCell>
                            <TableCell><SalaryComponentInput value={hra} onInputChange={setHra} /></TableCell>
                        </TableRow>
                        <TableRow>
                            <TableCell>Other Allowances</TableCell>
                            <TableCell><SalaryComponentInput value={otherAllowances} onInputChange={setAllowances} /></TableCell>
                        </TableRow>
                        <TableRow>
                            <TableCell>Employer's PF contribution</TableCell>
                            <TableCell><SalaryComponentInput value={providendFund} onInputChange={setProvidendFund} /></TableCell>
                        </TableRow>
                        <TableRow>
                            <TableCell>Gratuity</TableCell>
                            <TableCell><SalaryComponentInput value={gratuity} onInputChange={setGratuity} /></TableCell>
                        </TableRow>
                        <TableRow>
                            <TableCell>Variable Pay</TableCell>
                            <TableCell><SalaryComponentInput value={variable} onInputChange={setVariable} /></TableCell>
                        </TableRow>
                        <TableRow>
                            <TableCell>Bonus</TableCell>
                            <TableCell><SalaryComponentInput value={bonus} onInputChange={setBonus} /></TableCell>
                        </TableRow>
                        <TableRow>
                            <TableCell>Total Annual CTC</TableCell>
                            <TableCell><SalaryComponentOutput outputValue={componentsTotal()} /></TableCell>
                        </TableRow>
                    </TableBody>
                </FormTable>
            </>
        )
    }

    return (
        <>
            <Grid container xs={12}>
                <SectionTitle title="Salary Structure" />
                {formInputs()}
            </Grid>
        </>
    )
}