import { Grid } from "@mui/material"
import { Text, TextSizeType } from "gestalt"

type SectionTitleProps = {
    title: any
    sx?: any
    size?: TextSizeType,
    darkTheme?: boolean
}

const defaultSx = {
    mt: 5,
    mb: 2
}

export const SectionTitle = (props: SectionTitleProps) => {

    const convertToHref = (text: any) => {
        if (typeof text === "string") return text.replace(/\s+/g, '-').toLowerCase()
    }

    const { title, sx, size, darkTheme } = props

    return <Grid container xs={12} sx={sx ?? defaultSx} id={convertToHref(title)}>
        <Grid item xs={12}>
            <Text size={size ?? "400"} align="center" color={darkTheme ? "light" : "dark"}>
                {title}
            </Text>
        </Grid>
    </Grid>
}