import financialYearReducer from './financialYear'
import taxRegimeReducer from './taxRegime'
import salaryBreakupReducer from './salary'
import navigationReducer from './navigation'
import rentAndInvestmentsReducer from './rentAndInvestment'
import professionalTaxReducer from './professionalTax'
import { combineReducers, configureStore } from '@reduxjs/toolkit'

const reducer = combineReducers({
    financialYear: financialYearReducer,
    taxRegime: taxRegimeReducer,
    salaryBreakup: salaryBreakupReducer,
    navigation: navigationReducer,
    rentAndInvestments: rentAndInvestmentsReducer,
    professionalTax: professionalTaxReducer,
})

const store = configureStore({
    reducer,
})

export default store;