import { listSuportedFinancialYears } from "@harshit-kushwaha/tax-calculator-lib"
import { FinancialYear } from "../types/financialYear.type"

export const useFinancialYears = () => {

    const financialYearOptions: FinancialYear[] = [...listSuportedFinancialYears()]

    const defaultFinancialYear = getDefaultFinancialYear(financialYearOptions)

    return { financialYearOptions, defaultFinancialYear }
}

const getDefaultFinancialYear = (financialYearOptions: FinancialYear[]): string => {
    return financialYearOptions[0].id
}
