import { Stack, Switch, TableBody, TableCell, TableHead, TableRow, Typography } from "@mui/material"
import { SectionTitle } from "./formElements/SectionTitle"
import { SalaryComponentInput } from "./formElements/SalaryComponentInput"
import { useDispatch, useSelector } from "react-redux"
import { setMetroCity, setMonthlyRent } from "../store/rentAndInvestment"
import { SalaryComponentOutput } from "./formElements/SalaryComponentOutput"
import { SectionNotApplicable } from "./formElements/SectionNotApplicable"
import { RECOMMENDED_TAX_REGIME, isHraDeductibeForTaxRegimeId } from "@harshit-kushwaha/tax-calculator-lib"
import { CustomInfoIcon } from "./formElements/InfoIcon"
import { FormTable } from "./formElements/FormTable"

export const RentExpense = () => {

    const title = <SectionTitle title="Rent" />

    const { monthlyRent, metroCity } = useSelector<any, any>(state => state.rentAndInvestments)
    const { selectedRegime } = useSelector<any, any>(state => state.taxRegime)

    const dispatch = useDispatch()

    const isHraApplicable = isHraDeductibeForTaxRegimeId(selectedRegime) || selectedRegime === RECOMMENDED_TAX_REGIME
    
    const annualRent = monthlyRent * 12

    const handleMetroCityChange = (_event: any, value: boolean) => {
        dispatch(setMetroCity(value))
    }

    const rentInput = () => {
        return (
            <FormTable size="small" stickyHeader>
                <TableHead>
                    <TableCell>Rental</TableCell>
                    <TableCell>Amount</TableCell>
                </TableHead>
                <TableBody>
                    <TableRow>
                        <TableCell>Monthly</TableCell>
                        <TableCell><SalaryComponentInput value={monthlyRent} onInputChange={setMonthlyRent} /></TableCell>
                    </TableRow>
                    <TableRow>
                        <TableCell>Annual (Calculated)</TableCell>
                        <TableCell><SalaryComponentOutput outputValue={annualRent} /></TableCell>
                    </TableRow>
                    <TableRow>
                        <TableCell>Metro City <CustomInfoIcon link="#metro-cities-for-hra" /> </TableCell>
                        <TableCell>
                            <Stack direction="row" spacing={1} alignItems="center">
                                <Typography>No</Typography>
                                <Switch
                                    checked={metroCity}
                                    onChange={handleMetroCityChange}
                                    inputProps={{ 'aria-label': 'controlled' }}
                                />
                                <Typography>Yes</Typography>
                            </Stack>
                        </TableCell>
                    </TableRow>
                </TableBody>
            </FormTable>
        )
    }

    const sectionNA = () => (<SectionNotApplicable sectionName="HRA" />)

    return (
        <>
            {title}
            {
                isHraApplicable
                    ? rentInput()
                    : sectionNA()
            }
        </>
    )

}