import { Grid, TableBody, TableRow, TableCell } from "@mui/material"
import { SectionTitle } from "./formElements/SectionTitle"
import { SalaryComponentInput } from "./formElements/SalaryComponentInput"
import { useSelector } from "react-redux"
import { setMonthlyProfessionalTax } from "../store/professionalTax"
import { CustomInfoIcon } from "./formElements/InfoIcon"
import { FormTable } from "./formElements/FormTable"

export const ProfessionalTax = () => {

    const monthlyProfessionalTax = useSelector<any, number>(state => state.professionalTax.monthlyProfessionalTax)

    return (
        <Grid container xs={12}>
            <SectionTitle title="Professional Tax" />
            <FormTable size="small" stickyHeader>
                <TableBody>
                    <TableRow>
                        <TableCell>Professional Tax (p.m.) <CustomInfoIcon link="#professional-tax" /></TableCell>
                        <TableCell><SalaryComponentInput value={monthlyProfessionalTax} onInputChange={setMonthlyProfessionalTax} /></TableCell>
                    </TableRow>
                </TableBody>
            </FormTable>
        </Grid>
    )
}