import { LITERALS } from "../../utils/constants"
import { TextField } from "gestalt"

type SalaryComponentInputOutput = {
    outputValue: number,
    strikedValue?: number
}

export const SalaryComponentOutput = (props: SalaryComponentInputOutput) => {

    const { outputValue } = { ...props }

    const formatToCurrency = (value: number) => {
        return `${LITERALS.INDIAN_RUPEE} ${value.toLocaleString('en-IN')}`;
    }

    const output = () => {
        return `${formatToCurrency(outputValue)}`
    }

    return (
        <TextField
            id=""
            value={output()}
            size="sm"
            disabled
            onChange={() => { /* do nothing  */ } }
        />
    )
}