import { createSlice } from "@reduxjs/toolkit";
import { SalaryBreakup } from "../types/SalaryBreakup.type";

const initialState: SalaryBreakup = {
    basic: 0,
    hra: 0,
    otherAllowances: 0,
    providendFund: 0,
    gratuity: 0,
    variable: 0,
    bonus: 0
}

// Reducer
const slice = createSlice({
    name: 'salaryBreakup',
    initialState,
    reducers: {
        setBasic: (state, action) => {
            state.basic = action.payload;
        },
        setHra: (state, action) => {
            state.hra = action.payload;
        },
        setAllowances: (state, action) => {
            state.otherAllowances = action.payload;
        },
        setProvidendFund: (state, action) => {
            state.providendFund = action.payload;
        },
        setGratuity: (state, action) => {
            state.gratuity = action.payload;
        },
        setVariable: (state, action) => {
            state.variable = action.payload;
        },
        setBonus: (state, action) => {
            state.bonus = action.payload;
        },
        resetSalaryBreakup: () => initialState
    },
});

export default slice.reducer

// Actions
export const { setBasic, setHra, setAllowances, setProvidendFund, setGratuity, setVariable, setBonus, resetSalaryBreakup } = slice.actions