import { Box } from "gestalt";

type SectionCardProps = {
  children?: any,
  dark?: boolean,
  borderStyle?: "shadow" | "none"
}

export function SectionCard(props: SectionCardProps) {

  const { children, dark, borderStyle } = props

  return <Box padding={4} marginTop={4} marginBottom={4} color={ dark ? "dark" : "light" } rounding={5} borderStyle={borderStyle ?? "shadow"}>
    {children}
  </Box>
}